import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from "../views/home.vue"
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    children: [
      {
        path: '/',
        redirect: "/productList"
      },
      {
        path: "/productList",
        name: "productList",
        component: () => import("@/views/productList/index")
      },
      {
        path: "/productDetail/:id",
        name: "productDetail",
        component: () => import("@/views/productDetail/index")
      },
      {
        path: "/productBuy/:id",
        name: "productBuy",
        component: () => import("@/views/productBuy/index")
      },
      {
        path: "/paySuccess/:id",
        name: "paySuccess",
        component: () => import("@/views/paySuccess/index")
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior (to, from, savedPosition) {
    // return 期望滚动到哪个的位置
    return { x: 0, y: 0 };
  }
})

export default router
